#root{
	height: 100%;
}

#wrap{
	min-height: 100%;
}

#header{
	z-index: 200;
	position: sticky;
	top: 0;
	display: flex;
	align-items: center;
	gap: 0 100px;
	// justify-content: space-between;
	height: 100px;
	padding: 0 30px;
	background: #000a23;
	h1{
		.logo{
			@include font(38px, $white);
			font-weight: $fw-bold;
		}
	}
	.gnb{
		display: flex;
		align-items: center;
		gap: 0 30px;
		& > li{
			.btn-menu{
				@include font(24px, $white);
				font-weight: $fw-bold;
			}
		}
	}
}

#container{
	padding-bottom: 30px;
}

#footer{
	@include center(self,null,null,0,0);
	display: flex;
	align-items: center;
	justify-content: flex-end;
	width: 100%;
	height: 30px;
	padding: 0 30px;
	background: $bg-ea;
	@include font(12px, $black-9);
}

.main{
	padding: 50px;
	&-list{
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 30px;
		& > li{
			border: 1px solid $bg-ea;
			.thumb{
				width: 100%;
				height: 200px;
				img{
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			.txt-box{
				padding: 20px;
				.name{
					margin-top: 10px;
					@include font(20px, $black-3);
					font-weight: $fw-medium;
				}
				.date{
					margin-top: 10px;
					@include font(14px, $black-8);
				}
			}
		}
	}
}



.rating{
	display: flex;
	align-items: center;
	gap: 0 5px;
	.btn-rate{
		width: 20px;
		height: 20px;
		background: url("../../public/rate_def.png") no-repeat 0 0;
		background-size: 100%;
		&.active{
			background: url("../../public/rate_active.png") no-repeat 0 0;
			background-size: 100%;
		}
	}
}